.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  background-color: rgba(0, 0, 0, .8);
  transition: opacity .3s cubic-bezier(.38, .53, .8, .71), visibility .3s cubic-bezier(.38, .53, .8, .71);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden.modal {
  opacity: 0;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.modal {
  width: 280px;
  background-color: #f7f7f7;
  border-radius: 6px;
  padding: 50px 20px 30px;
  transition: transform .3s cubic-bezier(.38, .53, .8, .71), opacity .3s cubic-bezier(.38, .53, .8, .71);
  position: relative;
  top: 20px;
  left: 50%;
  overflow: hidden;
  transform: translateX(-50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

